import React, { useState } from 'react'
import { Check, Lock, Mail } from 'react-feather'
import { FormattedMessage } from 'react-intl'
import { Link } from 'react-router-dom'
import { CardBody, FormGroup, Label, Button, Input, Form, Row, Col, Card, CardHeader, CardTitle } from 'reactstrap'
import '../../assets/scss/pages/authentication.scss'
import CheckBox from '../../components/Checkbox'
import { history } from '../../history'

const Login = ({ loginAction }) => {
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')

  const onClickLogin = () => {
    loginAction({ username, password })
  }

  return (
    <Row className='m-0 justify-content-center'>
      <Col
        sm='8'
        xl='7'
        lg='10'
        md='8'
        className='d-flex justify-content-center'
      >
        <Card className='bg-authentication login-card rounded-0 mb-0 w-100'>
          <Row className='m-0'>
            <Col
              lg='6'
              className='d-lg-block d-none text-center align-self-center px-1 py-0'
            >
              <img src={''} alt='loginImg' />
            </Col>
            <Col lg='6' md='12' className='p-0'>
              <Card className='rounded-0 mb-0 px-2 login-tabs-container'>
                <CardHeader className='pb-1'>
                  <CardTitle>
                    <h4 className='mb-0'>Login</h4>
                  </CardTitle>
                </CardHeader>
                <p className='px-2 auth-title'>
                  Welcome back, please login to your account.
                </p>
                <React.Fragment>
                  <CardBody className='pt-1'>
                    <Form action='/' onSubmit={(e) => e.preventDefault()}>
                      <FormGroup className='form-label-group position-relative has-icon-left'>
                        <Input type='email' placeholder='Email' required />
                        <div className='form-control-position'>
                          <Mail size={15} />
                        </div>
                        <Label>Email</Label>
                      </FormGroup>
                      <FormGroup className='form-label-group position-relative has-icon-left'>
                        <Input
                          type='password'
                          placeholder='Password'
                          required
                        />
                        <div className='form-control-position'>
                          <Lock size={15} />
                        </div>
                        <Label>Password</Label>
                      </FormGroup>
                      <FormGroup className='d-flex justify-content-between align-items-center'>
                        <CheckBox
                          color='primary'
                          icon={<Check className='vx-icon' size={16} />}
                          label='Remember me'
                          defaultChecked={false}
                        />
                        <div className='float-right'>
                          <Link to='/pages/forgot-password'>
                            Forgot Password?
                          </Link>
                        </div>
                      </FormGroup>
                      <div className='d-flex justify-content-between'>
                        <Button.Ripple
                          color='primary'
                          outline
                          onClick={() => {
                            history.push('/pages/register')
                          }}
                        >
                          Register
                        </Button.Ripple>
                        <Button.Ripple color='primary' type='submit' onClick={onClickLogin}>
                          Login
                        </Button.Ripple>
                      </div>
                    </Form>
                  </CardBody>
                </React.Fragment>
              </Card>
            </Col>
          </Row>
        </Card>
      </Col>
    </Row>
  )
}

export default Login
