import { LOAD_NATIVGATION } from '../../actions/navbar/Index'

const initialState = {
  navConfigs: []
}

const navbarReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOAD_NATIVGATION:
      return {...state, navConfigs : [...action.payload]}
    default:
      return state
  }
}

export default navbarReducer
